// background themes

.bg {background-color: $clr-4;}

// strip

div.strip {
  position: relative;
  padding: ($spacing*1.5) 0;
  @include bp-m {padding: ($spacing*2) 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);
    @include bp-m {padding: ($spacing*3) 0 ($spacing*2);}

    div.wrap > h2 {@extend .h1;}
  }
}

// page intro

div.page-intro {
  position: relative;
  z-index: 2;
  padding: ($spacing*5.5) 0 0;
  text-align: center;

  body.alt & {padding-bottom: 0;}

  @include bp-m {
    padding: ($vmargin*6) 0 ($vmargin*3);

    &:after { // blue side column
      position: absolute;
      right: 0; top: 0;
      z-index: 1;
      width: 30%;
      height: 160px;
      background: $clr-b2;
      content: "";
    }

    &.with-image {
      text-align: left;

      &:after {
        bottom: 0;
        height: auto;
      }
    }
  }

  div.wrap {
    position: relative;
    z-index: 3;
    > :last-child {margin-bottom: 0;}
  }

  figure {
    margin: ($vmargin*1.5) 0 0;
    border: solid $clr-b2;
    border-width: 0 0 10px;

    @include bp-m {
      position: absolute;
      right: 0; top: 180px;
      z-index: 2;
      margin: 0;
      border-width: 10px 0 10px 10px;
      width: 40%;
    }

    @include bp-l {width: 45%;}
    @include bp-xl {max-width: 750px;}
  }

  h1 {
    margin-bottom: 0;

    body.error & {
      @extend .h2;

      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }

    span {
      @include bp-m {display: block;}
    }
  }

  p {

    &.lead {
      margin: 0.75rem 0 2rem;

      div.home-intro & {
        margin-bottom: 0;
        @include bp-m {margin-bottom: $vmargin*2;}
      }

      div.home-intro & {
        @include bp-m {font-size: $fs-35; font-weight: 200;}
        @include bp-l {font-size: $fs-40;}
      }

      span {@include bp-m {display: block;}}
    }
  }
}

// call to actions

main.content {
  > div:last-child:is([class*="cta"]) {
    background: $clr-1;
    color: white;

    div.wrap {
      blockquote {
        div.quote {
          background: $clr-2;

          &:after {
            border-color: $clr-2 transparent transparent $clr-2;
            @include bp-m {border-color: $clr-2 $clr-2 transparent transparent;}
          }

          &.single:after {
            @include bp-m {border-color: $clr-2 $clr-2 transparent transparent;}
          }
        }
      }
    }
  }
}

div.strip.cta {
  position: relative;
  z-index: 50;
  padding: ($spacing*1.5) 0;
  background-color: $clr-3;
  text-align: center;

  @include bp-m {
    text-align: left;
    &.download {padding: $spacing 0;}
  }

  &.client-feedback {
    padding: ($spacing*2) 0;

    div.wrap {
      display: block;

      > h2 {
        position: relative;
        z-index: 1;
        margin-bottom: $vmargin;
        font-size: $fs-30;

        @include bp-m {
          width: $col-5;
          margin: 0 0 -2rem;
        }

        a {
          text-decoration-line: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration-line: underline;}
          }
        }
      }

      blockquote {
        margin: 0;
        
        @include bp-m {display: flex;}

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: darken($clr-3,5%);
          text-align: center;

          @include bp-m {
            flex: 1 1 auto;
            order: 2;
          }

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: darken($clr-3,5%) transparent transparent darken($clr-3,5%);
            content: "";

            @include bp-m {
              top: auto; left: auto; bottom: 0.7rem; right: 100%;
              margin-left: -10px;
              border-color: darken($clr-3,5%) darken($clr-3,5%) transparent transparent;
            }
          }

          >:last-child {margin-bottom: 0;}

          p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }
        }

        div.source {
          margin-top: ($vmargin/2);

          @include bp-m {
            flex: 0 0 auto;
            display: flex;
            align-items: flex-end;
            margin: 0;
            width: 16rem;

            &.download {width: 18rem;}
          }

          span.initials {
            display: inline-block;
            vertical-align: middle;
            margin: 0.75rem 0.75rem 0;
            border-radius: 3rem;
            width: 2.75rem;
            height: 2.75rem;
            line-height: 2.75rem;
            background: $clr-b2;
            text-decoration: none;
            text-align: center;
            font-size: $fs-22;
            font-weight: 300;
            color: white;

            @include bp-m {
              flex: 0 0 auto;
              order: 2;
              margin: 0 1.4rem 0 0.75rem;
            }
          }

          p {
            display: inline-block;
            vertical-align: middle;
            margin: 0.75rem 0 0;
            text-align: center;
            font-size: $fs-16;

            @include bp-s {text-align: left;}

            @include bp-m {
              flex: 1;
              margin: 0;
              text-align: right;
            }

            span {display: block;}
          }
        }
      }
    }
  }

  &.double-testimonial {
    div.wrap {
      display: block;

      ul {
        margin-bottom: 0;

        @include bp-m-less {
          li:first-child {margin-bottom: $vmargin;}
        }

        @include bp-m {
          margin-top: -3rem;
          li {display: flex;}
        }
      }

      blockquote {
        margin: 0;

        @include bp-m {
          display: flex;
          flex-direction: column;
        }

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: white;
          text-align: center;
          box-shadow: 0 0 20px 5px rgba(black,0.10);

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          @include bp-m {
            flex: 1 1 auto;
            display: flex;
            align-items: center;
          }

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: white transparent transparent white;
            content: "";
          }

          >:last-child {margin-bottom: 0;}

          p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }
        }

        div.source {
          margin-top: ($vmargin);
          text-align: center;

          @include bp-m {
            flex: 0 0 auto;
          }

          span.initials {
            display: inline-block;
            vertical-align: middle;
            border-radius: 3rem;
            width: 2.75rem;
            height: 2.75rem;
            line-height: 2.75rem;
            background: rgba(black,0.5);
            text-decoration: none;
            text-align: center;
            font-size: $fs-22;
            font-weight: 300;
            color: white;
          }

          p {
            margin: 0.75rem 0 0;
            font-size: $fs-16;

            span {display: block;}
          }
        }
      }
    }
  }

  div.wrap {

    @include bp-m {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    @include bp-l {align-items: flex-end;}

    > h2 {
      @include lead();
      margin: 0;

      @include bp-m {
        flex: 0 1 auto;
        align-self: center;
      }
    }

    > p,
    div.audio-insert {
      margin: $vmargin auto 0;

      @include bp-m {
        flex: 0 0 auto;
        align-self: center;
        margin: 0 0 0 $spacing;
      }

      a.button {
        @include bp-s-less {display: block;}
      }

      span.other {
        display: block;
        margin-top: 0.75rem;
        font-size: $fs-18;
        text-align: center;

        a {color: white;}
      }
    }
  }

  @include bp-l {
    &.download,
    &.webinar,
    &.audio {

      body.guide & {margin-top: ($vmargin*2);}

      span.image {
        flex: 0 0 auto;
        display: block;
        position: relative;
        width: 280px;
      
        &:before {
          position: absolute;
          left: -($spacing); bottom: -($spacing*1.5);
          padding-top: 69%;
          width: 100%;
          background: no-repeat 50% 100%;
          background-size: 100% auto;
          content: "";
        }
      }

      div.wrap h2 {flex: 1 1 auto;}
    }

    &.download {
      span.image:before {bottom: -($spacing+0.02);}

      a.button {
        @include bp-m {margin: 0.75rem 0;}
      }
    }

    &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
  }
}


// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  //border-radius: $border-radius;

  &.spacer-up + p {@extend .spacer-up;}

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}
    }
  }
}

// announcement

div.announcement {
  padding: $spacing 0;
  background: $clr-warning;
  text-align: center;

  div.page-intro & {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
  }

  div.wrap > :last-child {margin-bottom: 0;}

  span.text {
    @include bp-m {display: inline-block;}
  }

  .button {
    margin-top: ($vmargin/2);
    @include bp-l {margin: 0 0 0 ($vmargin/2);}
  }
}

// promotions

div.promotion {
  padding: ($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-4;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}

// pdf embed

div.download-sample {
  @include bp-m-less {text-align: center;}
}

div.sample-preview {
  display: flex;
  align-items: stretch;
}

div.pdf-insert {
  flex: 1;
  position: relative;
  border-bottom: 5px solid #4F5356;
  background: #4F5356;
  box-shadow: 0 0 10px 5px rgba(black,0.1);

  @-moz-document url-prefix() {
    border-bottom: 20px solid #2A2A2E;
    background: #2A2A2E;
  }
  
  @include bp-m-less {display: none;}

  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: rgba(white,0.7);
  }
}

p.pdf-legacy {
  padding: ($spacing*2);
  text-align: center;

  > span {
    display: block;
    margin-bottom: $vmargin;
  }
}



// videos

div.video-wrap {
  margin-bottom: $vmargin;

  .box & {margin-bottom: 0;}

  &.rounded {
    overflow: hidden;
    border-radius: $border-radius;
  }

  div.video-insert,
  p.video-caption {
    //margin-left: (-$gutter); margin-right: (-$gutter);

    @include bp-m {
      margin: 0 auto;
      max-width: 600px;

      article & {
        float: right;
        clear: both;
        position: relative;
        z-index: 1;
        margin-left: $spacing;
        width: 50%;
      }

      &.playing,
      &.playing + p.video-caption {
        float: none;
        margin-left: 0;
        width: auto;
        max-width: none;
      }
    }
  }

  div.video-insert {
    position: relative;

    &.playing {
      &:before {
        display: block;
        padding-top: 56.23%;
        content: "";
      }
    }

    // &.playing,
    // a.cta {
    //   background: black;
    // }

    a.cta {
      position: relative;
      display: block;
      transition: filter 0.2s;

      @include bp-m {
        &:hover,
        &:focus {
          filter: brightness(90%);

          &:before {
            background-size: 15%;
            opacity: 1;
          }
        }
      }

      &:before {
        position: relative;
        z-index: 2;
        display: block;
        padding-top: 56.23%;
        background: url(../../assets/images/content/other/play.png) no-repeat 50%;
        background-size: 10%;
        cursor: pointer;
        content: "";
        opacity: 0.75;
        transition: background-size 0.1s, opacity 0.1s;
      }

      img {
        position: absolute;
        right: 0; top: 0; left: 0; bottom: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
    }

    object,
    video,
    iframe {
      position: absolute;
      top: 0; left: 0;
      border: 0;
      width: 100%;
      height: 100%;
      background: transparent;
    }
  }

  p.video-caption {
    margin-bottom: $vmargin;
    border-radius: 0 0 0.5rem 0.5rem;
    padding: 0.5rem 1rem;
    background: $clr-4;
    font-size: $fs-16;
  }
}


// testimonials

div.testimonials {
  margin: $vmargin 0;

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        @include hide-input();

        &:checked {
          + div.content {
            overflow: visible;
            //visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        //visibility: hidden; - don't add this because it causes Google Lighthouse to complain about hidden labels
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 750px;
          min-height: 18rem;
          background: $clr-4;

          .bg-b5 & {background: rgba($clr-1,0.05);}

          @include bp-s {
            padding: $spacing;
            min-height: 22rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          div.quote {
            font-size: $fs-18;
            @include bp-m {font-size: $fs-20;}

            &.small {
              @include bp-s-less {
                p:last-child {@include hide-screen();}
              }
            }

            &.medium {
              font-size: $fs-22;
              @include bp-m {font-size: $fs-25;}
            }
             
            &.large {
              font-size: $fs-25;
              font-weight: 300;

              @include bp-s {font-size: $fs-30;}

              @include bp-m {
                font-size: $fs-40;
                font-weight: 200;
              }
            }
              
            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          p.source {
            margin-bottom: 0;
            font-style: normal;
            font-weight: 700;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-label();}
        }
      }
    }
  }
}
