footer.site {
  padding: ($spacing*2) 0;
  font-size: $fs-15;

  @include bp-m {text-align: left;}

  div.row {align-items: end;}

  // newsletter

  div.newsletter {
    h2 {
      margin-bottom: 0.25rem;
      font-size: $fs-18;
      font-weight: 700;
    }
  }

  // company

  div.company {
    text-align: center;

    @include bp-m {text-align: left;}

    h2 {
      display: inline-block;
      margin: $spacing 0 ($spacing/1.75);
      width: 150px;
      background: url(../../assets/images/logos/beyond-the-ward.svg) no-repeat 50% 100%; 
      background-size: 100% auto;

      @include bp-m {margin: 0 0 0.5rem;}

      &:before {
        display: block;
        padding-top: 57.5%;
        content: "";
      }

      span {@include hide-screen();}
    }

    a {
      color: $clr-1;
      text-decoration-line: none;

      @include bp-m {
        &:hover,
        &:focus {
          text-decoration-line: underline;
          color: $clr-b1;
        }
      }
    }

    p {
      margin: 0;
      span {display: block;}
    }

    ul.links {
      margin: 0;
      @include bp-m {display: inline-block;}

      li {
        display: inline-block;
        margin: 0 0.25rem;
        @include bp-m {margin-left: 0;}
      }

      a {font-weight: 400;}
    }
  }
}
