header.banner {
  position: absolute;
  top: 0; left: 0; right: 0;

  body.analytics & {display: none;}
  
  div.wrap {

    a.content-jump {
      position: absolute;
      top: 0; left: -1000em;
      z-index: 1000;
      padding: $nav-link-padding 0.4rem;
      background: $clr-b1;
      text-align: center;
      color: white;
      text-decoration: none;
      outline: none;

      &:active,
      &:focus {left: 0; right: 0;}
    }
  }

  // newsletter

  div.newsletter {
    display: none;

    @include bp-m {
      display: block;
      position: absolute;
      top: 1rem; left: 72%;
      z-index: 10;
      width: 26%;
    }

    @include bp-xl {width: 350px;}

    h2 {
      margin-bottom: 0.2rem;
      font-size: $fs-17;
      font-weight: 700;
      color: white;
    }
  }

  // nav

  nav {
    position: relative;
    z-index: 100;
    display: flex;
    align-items: start;
    padding-top: $spacing;

    @include bp-m {width: 72%;}

    // menu toggle

    p.menu-toggle {
      position: fixed;
      top: 0; right: 0;
      z-index: 3;
      padding: 1rem 1rem 0 0;
      line-height: 1;
      cursor: pointer;

      @include bp-m {@include hide-screen();}

      label.button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3rem;
        padding: 0;
        width: 2.5rem;
        height: 2.5rem;
        background: white;
        font-size: $fs-25;
        color: $clr-1;

        @include bp-m {
          width: 3rem;
          height: 3rem;
          font-size: $fs-30;
              
          &:hover,
          &:focus {
            background: $clr-b1;
            color: white;
          }
        }

        &:before {
          @extend .icon-extension,.icon-bars;
        }

        span {@include hide-screen();}
      }
    }

    // logo

    p.logo {
      flex: 0 0 auto;
      position: relative;
      z-index: 3;
      margin: 0;
      width: 150px;
      font-size: $fs-22;

      @include bp-m {width: 150px;}
      @include bp-l {width: 190px;}

      a {
        display: block;
        background: url(../../assets/images/logos/beyond-the-ward.svg) no-repeat 50% 72%; 
        background-size: 100% auto;
        text-decoration: none;

        &:after {
          display: block;
          padding-top: 57.5%;
          content: "";
        }

        > span {@include hide-screen();}
      }
    }

    // links

    div.links {
      position: absolute;
      left: -1000em;
      text-align: center;

      @include bp-m {
        position: static;
        display: flex;
        justify-content: center;
        flex: 1 1 auto;
        width: auto;
      }

      a {text-decoration: none;}

      ul.site {
        margin: 0;
        font-size: $fs-24;

        @include bp-m {
          display: flex;
          align-items: center;
          font-size: $fs-22;
        }

        @include bp-l {
          margin-top: 0.4rem;
          font-size: $fs-24;
        }

        li {

          @include bp-m {
            flex: 1 0 auto;
            position: relative;
          }

          a {
            display: block;
            padding: 0.5rem 0.75rem;
            font-weight: 700;
            color: white;

            @include bp-m {
              flex: 1 0 auto;
              border-radius: 0;
              padding: 0.5rem 1rem;
              background: none;
              color: $clr-b1;

              &:hover,
              &:focus {
                background: $clr-4;
                color: $clr-1;
              }
            }

            &.current {
              background: $clr-b1;
              color: white;
            }
          }
        }
      }
    }
  }

  // menu toggle 

  input#menu-toggle {
    @include hide-input();

    // -------------------------------------- menu open

    &:checked + nav {
      display: block;
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 2000;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      padding-top: 5rem;
      width: 100%;
      background: $clr-b1;

      // menu toggle

      p.menu-toggle {
        label.button {
          &:before {@extend .icon-times;}
        }
      }

      // logo

      p.logo {
        position: absolute;
        top: 1rem; left: 1rem;
        z-index: 3;
        margin: 0;
        padding: 0;

        a {
          @extend .button;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 2;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          background: white;
          font-size: $fs-25;
          color: $clr-1;

          &:before {
            @extend .icon-extension, .icon-home;
            padding: 0;
          }

          > span {@include hide-screen();}
        }
      }

      // links

      div.links {
        position: relative;
        left: auto;
        z-index: 2;
        margin: 0 1rem 2rem;
        font-size: $fs-17;

        ul.site {
          li {
            margin-bottom: 2px;
            background: rgba(black,0.3);

            a {
              display: flex;
              flex-direction: column;
              padding: 0.6rem 1rem;
              background: rgba(black,0.1);
              font-size: $fs-25;
              font-weight: 300;
              color: white;
              transition: background-color 0.1s;

              &.current {
                box-shadow: none;
                background: rgba(black,0.4);
              }
            }
          }
        }
      }
    }
  }
}
