html {
  font-size: $fs-base;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  background: $clr-3;
  text-align: left;
  font-family: $ff-1;
  font-weight: 400;
  line-height: $lh-general;
  color: $clr-1;
}

main.content {
  background: white;
}

// hiding

.hide-screen {
  @include hide-screen();

  &.focusable:active,
  &.focusable:focus {
    position: static;
    overflow: visible;
    clip: auto;
    margin: 0;
    width: auto;
    height: auto;
  }
}

.hide-mobile {
  @include bp-s-less {@include hide-screen();}
}

.hide {display: none!important;}

// interactive element focus

a,
button,
input,
select,
textarea {
  &:focus {outline: 1px dotted;}
}

// elements

embed,
iframe,
object {max-width: 100%;}

sup {
  font-size: 0.75rem;
  vertical-align: super;
}

code {
  font-family: monospace;
  line-height: 1.0;
}

figcaption {font-size: $fs-16;}
strong,b {font-weight: 700;}
strong b {color: $clr-b1;}

audio {width: 100%;}
audio::-webkit-media-controls-panel {
  background-color: $clr-3;
  color: $clr-1;
}

dl {
  dt {
    margin-top: 0.75rem;
    &:first-child {margin-top: 0;}
  }
}

q {font-style: italic;}

abbr,
dfn {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-thickness: 1px;
  text-underline-offset: 2px;
  font-style: normal;
  cursor: help;
}

// anchors

a {
  font-weight: 700;
  color: $clr-b1;
  text-decoration-line: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 2px;
  cursor: pointer;

  @include bp-m {
    &:hover,
    &:focus {
      color: $clr-b1;
      text-decoration-line: none;
    }
  }
}

a.flip {
  text-decoration: none;

  @include bp-m {
    &:hover,
    &:focus {
      text-decoration-line: underline;
      text-decoration-thickness: 1px;
      text-underline-offset: 2px;
    }
  }
}

// buttons

.button.button { 
  display: inline-block;
  border-radius: 0;
  border: 0;
  padding: 0.75em 1.1em;
  background-color: $clr-b1;
  text-align: center;
  font-family: $ff-1;
  font-size: $fs-24;
  font-weight: 700;
  line-height: 1.1;
  color: white;
  cursor: pointer;
  text-decoration: none;
  text-shadow: none;
  box-shadow: 0 1px 2px rgba(black,0.3), inset 0 -2px 1px rgba(black,0.2);
  outline: none;
  user-select: none;
  -webkit-user-select: none;
  -webkit-appearance: none;
  transition: box-shadow 0.1s, background-color 0.1s, color 0.1s;

  &.big {@include bp-m {font-size: $fs-30;}}
  &.small {padding: 0.6em 0.85em; font-size: $fs-20;}
  &.full {width: 100%;}
  &.full-mobile {@include bp-s-less {display: block; width: 100%;}}

  &.secondary {
    background: white;
    color: $clr-b1;
    box-shadow: inset 0 0 0 2px $clr-b1;

    &.alt {
      background: rgba(black,0.2);
      color: white;
      box-shadow: inset 0 0 0 2px white, 0 1px 2px rgba(black,0.4);
    }
  }

  @include bp-m {

    &:hover,
    &:focus,
    a.pseudo-button:hover &,
    a.pseudo-button:focus & {
      background: lighten($clr-b1, 4%)!important;
      color: white!important;
    }

    &:active {
      background: $clr-b1!important;
      color: white!important;
      box-shadow: inset 0 1px 2px rgba(black,0.75)!important;
    }
  }

  // with icons

  span[class*="icon-"] {
    display: flex;
    justify-content: center;
    align-items: end;
    @-moz-document url-prefix() {align-items: center;}

    &:before {
      margin-right: 0.35em;
      font-size: 1.1em;
    }

    &.after:before {
      order: 2;
      margin: 0 0 0 0.3em;
      font-size: 0.9em;
    }

    > span > span {@include hide-screen();}
  }
}

a.amazon-button {
  display: block;
  max-width: 200px;
  transition: transform 0.15s;

  @include bp-m {
    &:hover,
    &:focus {transform: scale(1.025);}
  }

  img {
    display: block;
    width: 100%;
  }
}

// single link

a.single-link {
  text-decoration-line: none;
  font-weight: 700;

  @include bp-m {
    &:hover span,
    &:focus span {text-decoration-line: underline;}
  }

  &[class*="icon-"] {
    display: inline-flex;
    align-items: baseline;

    &:before {
      margin-right: 0.3em;
      font-size: 0.8em;
    }

    &.after:before {
      order: 2;
      margin: 0 0 0 0.2em;
    }
  }
}

// alignment

p, pre, blockquote, ul, ol, dl, figure, table {margin-bottom: $vmargin;}

// headings

h1,h2,h3,h4,h5,h6 {
  margin-bottom: $vmargin;
  font-weight: 300;
  font-size: $fs-20;
  line-height: $lh-headings;
}

h1 {font-weight: 200;}

h1,.h1 {
  font-size: $fs-35;
  
  @include bp-s {font-size: $fs-40;}

  @include bp-m {
    font-size: $fs-45;
    > span {display: inline-block;}
  }

  @include bp-l {
    font-size: $fs-55;
    > span {display: block;}
  }

  @include bp-xl {font-size: $fs-60;}
}

h2,.h2 {
  font-weight: 200;
  font-size: $fs-30;
  @include bp-m {
    font-size: $fs-35;

    &.consulting-landing {padding-top: $spacing;}
  }
}

.h2-ext {
  @extend .h2;
  @include bp-l {font-size: $fs-50;}
}

h3,.h3 {
  font-weight: 300;
  font-size: $fs-24;
  @include bp-m {font-size: $fs-27;}
}

h4,.h4 {
  font-weight: 400;
  font-size: $fs-22;
  @include bp-m {font-size: $fs-25;}
}

h5,.h5,dt {
  font-weight: 700;
  font-size: $fs-20;
  @include bp-m {font-size: $fs-22;}
}

h6,.h6 {font-weight: 700;}

// helpers

.break {display: block;}
.break-desktop {@include bp-m {display: block;}}
.no-margin {margin: 0;}
.no-wrap {white-space: nowrap;}
.text-wrap {overflow-wrap: break-word; word-wrap: break-word;}
.align-center {text-align: center;}
.align-left {text-align: left;}
.align-right {text-align: right;}
.align-middle {margin-left: auto; margin-right: auto;}
.small-text {font-size: 0.8em;}
.new-window {cursor: alias;}
.order-swap {order: 2;}
.margin-up {margin-top: $vmargin;}
.lead {@include lead();}
.button-extra {display: block; margin-top: 0.75rem;}
.spacer-down.spacer-down {@include bp-m {margin-bottom: ($vmargin*2);}}

.spacer-up {
  margin-top: $vmargin;
  @include bp-m {margin-top: ($vmargin*2);}
}

.target:target:before {
  display: block;
  margin-top: -1rem;
  height: 1rem;
  content: "";
  visibility: hidden;
}

.divide-up {
  margin-top: ($vmargin*1.5);
  border-top: 3px solid $clr-4;
  padding-top: $vmargin;
}

.box {
  margin-bottom: $vmargin;
  background: $clr-4;

  .bg & {background: rgba(white,0.5);}

  &.dark {
    background: $clr-2;
    color: white;

    a {color: white;}
  }

  h2 {@extend .h2-ext;}

  figure {margin-bottom: 0;}

  div.text {
    padding: $spacing;
    >:last-child {margin-bottom: 0;}
  }
}

p.address span {display: block;}

figure {

  &.avatar {
    display: block;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    width: 100%;

    img {
      position: absolute;
      top: 0; left: 0;
      z-index: 2;
      border-radius: 50%;
      width: 100%;
      height: auto;
    }

    &:after {
      display: block;
      padding-top: 100%;
      content: "";
    }
  }

  &.expanded {
    // margin-left: -$gutter;
    // margin-right: -$gutter;
    overflow: hidden;
    border-radius: $border-radius;
    background: $clr-4;

    @include bp-m {
      float: right;
      clear: both;
      margin-left: $spacing;
      max-width: 50%;
    }

    div.image {
      padding: 1rem;
      background: $clr-3;
    }

    figcaption {
      display: block;
      padding: 0.5rem 1rem;
    }
  }

  &.inline {
    overflow: hidden;
    border-radius: $border-radius;
    background: $clr-4;

    @include bp-m {
      float: right;
      clear: both;
      margin-left: $spacing;
      max-width: 50%;
    }

    div.image {background: $clr-3;}

    figcaption {
      display: block;
      padding: 0.5rem 1rem;
    }
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

p.statement {
  margin: 1rem 0 0;
  font-size: $fs-16;

  span {white-space: nowrap;}
}

blockquote.general {
  border-radius: $border-radius;
  padding: $spacing;
  background: $clr-4;

  > :last-child {margin-bottom: 0;}  

  p {
    @include lead();
  }   
}


