// unstyled list

.unstyled {
  margin-left: 0;
  list-style: none;
  li {margin: 0.5rem 0;}
}

// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// checked alternate

ul.checked-alternate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;
  font-size: $fs-18;

  @include bp-m {margin: 0;}

  li {
    position: relative;
    padding: 0.6rem 2rem;
    padding-left: 2.5rem;
    
    &:first-child {border-top: 0;}
    &:nth-child(odd) {background: darken($clr-4,2%);}

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0.8rem; top: 0.65rem;
      @-moz-document url-prefix() {top: 0.55rem;}
      font-size: $fs-25;
      color: $clr-success;
      .bg-dark & {color: white;}
    }
  }
}

// checked list

ul.checked {
  margin-left: 0!important;
  list-style: none!important;
  font-size: $fs-18;

  li {
    position: relative;
    margin: 0.4rem 0;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check;
      position: absolute;
      left: 0; top: 0.2em;
      color: $clr-b1;
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }

  div.product-overview & {
    text-align: left;
    font-size: $fs-20;
  }
}

// icons list

ul.icons {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin: 0.4rem 0;
    padding-left: 1.2em;

    &:before {
      @extend .icon-extension;
      position: absolute;
      left: 0; top: 0.2em;
    }
  }
}

// asterisk list

.asterisk {
  margin-left: 0!important;
  list-style: none!important;
  font-size: $fs-18;

  &.centered-list {
    display: inline-block;
    text-align: left;
  }

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.2em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.25em;
      color: $clr-b1;
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }
}

// styled list

ul.styled {
  border-radius: $border-radius;
  padding: ($spacing*0.5) $spacing;
  background: $clr-4;

  @include bp-m {font-size: $fs-20;}
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;
    }
  }

  &.plain a {
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration-line: underline;}
    }
  }
}

// buttons

ul.buttons {
  
  @include bp-s {
    display: flex;
    justify-content: flex-start;
  }

  li {
    margin: 0.5rem 0;

    @include bp-s {
      flex: 0 1 auto;
      margin: 0.5rem 0.25rem;
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: $fs-20;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}

// numbered discs

ol.numbered-discs {
  margin-left: 0;
  list-style: none;
  counter-reset: numbered-discs;

  body.error & {display: inline-block;}

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.5rem;
      height: 1.5rem;
      line-height: 1.45rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);
    }
  }
}

// funnels

ol.funnels {
  text-align: center;

  > li {

    a.content {
      display: block;
      background-color: rgba($clr-2,0.05);
      transition: background-color 0.15s, opacity 0.15s;
      text-decoration: none;

      @include bp-m {
        display: flex;
        flex-direction: column;
        flex: 1;

        &:hover,
        &:focus {
          background-color: rgba(white,0.2);

          img {opacity: 1;}
        }
      }

      img {
        display: block;
        width: 100%;
        height: auto;
        opacity: 0.9;
      }

      // Header

      div.header {
        padding: ($spacing/1.25) ($spacing/2);
        background-color: rgba(white,0.6);

        h3 {
          @extend .h4;
          margin: 0;
        }
      }

      // Text 

      div.text {
        flex: 1;
        padding: $spacing;
        //font-size: $fs-18;
        font-weight: 400;
        color: $clr-1;

        @include bp-m {flex: 1;}

        > :last-child {margin-bottom: 0;}
      }

      // CTA

      div.cta {
        flex: 0 1 auto;
        padding: 0 $spacing $spacing;
        font-size: $fs-18;

        > :last-child {margin-bottom: 0;}
      }
    }
  }
}

// site map

ul.site-map {

  li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }
  }
}

